.work {
  display: flex;
  width:45%;
  height: 100%;
  background-color: #f1f2f6;
  flex-direction: column;
  margin: 5px;
  color: white;
  box-sizing: border-box;
  @media screen and (max-width: 1090px) {
    width: 800px;
  }

  .image {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    height: 200px;
    width: 100%;
    background-size: cover;
    background-position:center;
    &:hover{
      &::before{
        content: '';
        position: absolute;
        background-color: black;
        opacity: 0.2;
        top:0;
        bottom: 0;
        left: 0;
        right: 0;
      }
    }

  }

  .description {
    display: flex;
    flex-direction: column;
    text-align: left;
    background-color: grey;
    box-sizing: border-box;

    .nameProject {
      margin: 5px;

    }

    .shortDescription {
      display: inline-block;
      margin: 5px;
    }
  }

}





