.wrapButton{
  display: inline-block;
  width: max-content;
  border: none;
  outline: none;
  background: none;
  .button{
    font-family: 'Work Sans', sans-serif;
    color: white;
    font-size: 14px;
    padding: 10px 15px;
    border-color: #ff7d15;
    background: #ff7d15;
    font-weight: 500;
    line-height: 24px;
    transition: all 0.5s;
    border-width: 2px;
    border-radius: 3px;
    text-decoration: none;

    &:hover{
      background-color: white;
      color: #ff7d15;
      cursor: pointer;
      opacity: 0.9;
    }
    &:active{
      //opacity: 0.5;
      background-color: #6c757d;
    }
  }
}