
.mainBlock {
  max-width: 1240px;
  margin: 0 auto;
  mix-height: 90vh;
  box-sizing: border-box;
  transition: all 1s;
  @media screen and (max-width: 1126px) {
    min-height: 90vh;
    flex-direction: column;
  }
  .particles{
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    right:0;
    opacity:0.3;
  }

  .mainContainer {
    display: flex;
    height: 100%;
    box-sizing: border-box;
    margin: 80px 0 30px 0;
    background-color: #2b2a2f;
    border-radius: 5px;
    padding: 20px;
    @media screen and (max-width: 1126px) {
      flex-direction: column;
    }
    @media screen and (max-width: 680px) {
      margin: 150px 0 30px 0;
    }
  }


  .textInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
    box-sizing: border-box;
    order: 2;
    transition: 1s;
    @media screen and (max-width: 1126px) {
      text-align: center;
      transition: 1s;
    }

    .divdiv {
      fill: #ff7d15;
      width: 50px;
      height: 50px;
    }

    .name {
      line-height: 1;
      padding-left: 4px;
      margin: 0 0 10px;
      color: white;
      font-weight: 500;
      font-size: 30px;
    }


    .title {
      line-height: 1;
      margin: 0;
      color: white;
      font-size: 80px;
      font-weight: 800;
      text-transform: capitalize;
      @media screen and (max-width: 1126px) {
        font-size: 40px;
      }
    }

    .subTitle {
      line-height: 1;
      display: inline-block;
      color: #ff7d15;
      font-size: 90px;
      font-weight: 800;
      @media screen and (max-width: 1126px) {
        font-size: 45px;
      }
    }


    .titleDescription {
      color: white;
      margin-bottom: 0;
    }

    .description {
      color: white;
      line-height: 2;
    }

    img {
      width: 40px;
      height: 40px;
    }

    .linkWrapper {
      p {
        color: white;
      }

      a {
        cursor: pointer;

        svg {
          cursor: pointer;
          fill: red;
          width: 50px;
          height: 50px;
        }
      }
    }
  }

  .wrapperPhotoBox {
    width: 100%;
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    align-items: center;
    padding: 0 15px;
    @media screen and (max-width: 1126px) {
      justify-content: center;
    }

    .boxPhoto {
      display: flex;
      justify-content: center;
      max-width: 500px;
      max-height: 500px;
      border-radius: 50%;
      @media screen and (max-width: 1126px) {
        max-width: 400px;
        max-height: 400px;
      }

      .photo {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }

  }

  @media screen and (max-width: 1126px) {
    min-height: 90vh;
  }
}
