.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1240px;
  margin: 0 auto;
  height: 100%;

  .wrapIcon {
    justify-content: center;
    .mainIconHi {
      width: 50px;
      height: 50px;
    }
  }
  .nav {
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 680px) {
      a:not(:first-child) {
        margin-top: 5px;
      }
      order: 3;
      flex-direction: column;
      text-align: center;
    }
    a {
      display: inline-block;
      font-weight: 500;
      font-size: 17px;
      text-decoration: none;
      padding: 0 20px;
      color: white;
      transition: 0.5s;
      box-sizing: border-box;
      //border-bottom: 2px solid #2b2a2f;
      @media screen and (max-width: 680px) {
        padding: 0px;
      }
      &:hover {
        color: #ff7d15;
        //border-bottom-color: white;
        box-sizing: border-box;
      }
      &:active{
        color: white;
        //border-bottom-color: #ff7d15;
      }
    }
  }
}


