.skill {
    display: flex;
    width: 150px;
    height: max-content;
    flex-direction: column;
    align-items: center;
    background-color: #f1f2f6;
    margin: 10px;
    box-sizing: border-box;
    transition: 1s;
    border-radius: 5px;
  //&:hover{
  //    box-shadow: 25px 18px 5px -5px rgba(169, 95, 46, 0.6);
  //      transform: scale(1.3);
  //}

    .skillIcon {
        margin-top: 10px;
        width: 100px;
        height: 100px;
    }
    .title {
        margin: 0;
        position: relative;
    }
    .description {
        text-align: center;
    }
}

@media screen and (max-width: 1200px) {
    .skill {
        padding-top: 5px;
    }
    .title {
        padding-top: 10px;
    }
}
@media screen and (max-width: 600px) {
    .skill {
        margin: 10px 0 0 0;
        transition: 0.6s;
    }
}

