.footer {
  width: 100%;
  min-height: 50px;
  background-color: #2b2a2f;
  display: flex;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 1140px;
    margin: 0 auto;
    height: 100%;
    box-sizing: border-box;
    @media screen and (max-width: 1040px) {
      padding-left:10px;
      padding-right:10px;
    }
    @media screen and (max-width: 420px) {
      flex-direction: column;
    }
    .right {
      color: #ff7d15;
      font-size: 20px;
      @media screen and (max-width: 420px) {
       order: 2;
      }
    }
    .items{
      display: flex;
      align-items: center;
      a{
        display: flex;
        align-items: center;
        height: max-content;
        padding: 3px;
      }
      img{
        width: 40px;
        height:40px;
      }
    }
  }
}


