.title {
  position: relative;
  color: #ff7d15;
  margin-top: 0px;
  font-weight: 700;
  font-size: 30px;

  &::before {
    content: "";
    width: 70px;
    height: 2px;
    background-color: white;
    position: absolute;
    bottom: -50%;
    left: 50%;
    transform: translate(-50%, 50%);

  }
}