.innerBlock {
  display: flex;
  height: 400px;
  @media screen and (max-width: 660px) {
    flex-direction: column;
    height: max-content;
  }

  .forma {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 100%;
    padding-right: 20px;
    box-sizing: border-box;
    align-items: center;
    @media screen and (max-width: 660px) {
     margin-bottom: 10px;
      width: 100%;
    }
    input {
      color: #f1f2f6;
      padding: 10px;
      margin: 2px;
      border: 2px solid #ff7d15;
      background: transparent;
      outline: none;
      width: 100%;
      box-sizing: border-box;
      &::placeholder{
        color:white;
        text-transform: uppercase;
        font-size: 12px;
      }
    }
    textarea {
      border: 2px solid #ff7d15;
      background: transparent;
      outline: none;
      color:white;
      width: 100%;
      height: 200px;
      box-sizing: border-box;
      margin: 2px;
      padding: 10px;
      resize: none;
      @media screen and (max-width: 660px) {
       height: 100px;
      margin-bottom: 10px;
      }
      &::placeholder{
        color:white;
        text-transform: uppercase;
        font-size: 13px;
      }
    }
  }

  .blockMap {
    width: 50%;
    @media screen and (max-width: 660px) {

      width: 100%;
    }
    iframe {
      box-sizing: border-box;
      width: 100%;
      height: 100%;
    }
  }
}


@media screen and (max-height: 820px) {
  .contactWrapper {
    padding: 20px 0 20px 0;
  }
}