.worksContainer {
        text-align: center;
        flex-wrap: wrap;
        .works {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            box-sizing: border-box;
          @media screen and (max-width: 1040px) {
            justify-content: center;
          }
        }
    }


@media screen and (max-height: 820px) {
    .WorksWrapper{
        padding: 20px 0 20px 0;
    }
}